* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Ubuntu', sans-serif;
}

.content {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;
}

.countDown {
    display: flex;
    margin: 3rem 0 0;
    justify-content: space-around;
    max-width: 100%;
    width: 320px;
}

.header {
    max-width: 90%;
}

.countDown li {
    list-style: none;
    color: #000;
    display: block;
    /* margin: 0 1.5rem; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 1.8rem;
}

.countDown li span {
    margin-right: .5rem;
    color: #349aa8;
    font-weight: 900;
    font-size: 180%;
}

.contentInner {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 0;
    right: 0;
    margin: auto;
    top: 0;
    bottom: 0;
}

.eggContainer {
    position: relative;
    -webkit-animation: shake 5s linear infinite;
            animation: shake 5s linear infinite;
}

.eggContainer::before {
    content: '';
    position: absolute;
    bottom: -2%;
    left: 10%;
    right: 0;
    margin: auto;
    background-color: #000;
    width: 50%;
    height: 3rem;
    border-radius: 50%;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    z-index: -1;
    opacity: .7;
}

@-webkit-keyframes shake {
    from, to {
        transform: translate3d(0, 0, 0);
    }
    1%, 3%, 5%, 7%, 9% {
        transform: translate3d(-10px, 0, 0);
    }
    2%, 4%, 6%, 8% {
        transform: translate3d(10px, 0, 0);
    }
    10% {
        transform: translate3d(0, 0, 0);
    }
}

@keyframes shake {
    from, to {
        transform: translate3d(0, 0, 0);
    }
    1%, 3%, 5%, 7%, 9% {
        transform: translate3d(-10px, 0, 0);
    }
    2%, 4%, 6%, 8% {
        transform: translate3d(10px, 0, 0);
    }
    10% {
        transform: translate3d(0, 0, 0);
    }
}
